import React, { Component } from 'react';
import {Consumer} from '../context/App-Context';
import SweetAlert from 'sweetalert2-react';
import { Link, Element , Events, animateScroll as scroll, scrollSpy, scroller } from 'react-scroll';



class VehicleSetting extends Component {
  constructor(){
    super()
    this.state={
      showSwalUpdateSuccess:false,
      showSwalDeleteAlert:false,
      showFormAdd:false,
      showFormEdit:false
    }
    
  }

shouldComponentUpdate(nextProps, nextState) {
        var script = document.createElement('script');
        script.src = "dist/js/pages/custom.js";
        document.getElementsByTagName('head')[0].appendChild(script);
        return true
}

  // componentDidMount() {
  //       var script = document.createElement('script');
  //       script.src = "dist/js/pages/custom.js";
  //       document.getElementsByTagName('head')[0].appendChild(script);
  //   }

  handleClickFormAdd=()=>{
      this.setState({showFormAdd:!this.state.showFormAdd,showFormEdit:false})
  } 

  render() {
    return (
      <div>
      <SweetAlert
                          show={this.state.showSwalUpdateSuccess}
                          title="Demo"
                          
                          confirmButtonText="Ok"
                          type="success"
                          text="SweetAlert in React"
                         
                          onConfirm={() => this.setState({showSwalUpdateSuccess:false})}
                          onCancel={() => this.setState({showSwalUpdateSuccess:false})}
                        />

                        <SweetAlert
                          show={this.state.showSwalDeleteAlert}
                          title="Demo"
                          
                          confirmButtonText="Ok"
                          type="warning"
                          text="SweetAlert in React"
                          showCancelButton={true}
                          onConfirm={() => this.setState({showSwalDeleteAlert:false})}
                          onCancel={() => this.setState({showSwalDeleteAlert:false})}
                        />

            <section className="content-header">
                   <ol className="breadcrumb">
                    
                    <li className="">Pengaturan Kendaraan</li>
                   </ol>
            </section>
            <div className="content"> 
                <div className="row  customPadding">



                <div className="box customPadding">
                    <div className="row">
                        <div className="col-md-12">
                          <div className="box-body">
                           

                           <div className="row">
                              <div className="col-md-10">
                             <Link activeClass="active" to="test1" spy={true} smooth={true} offset={50} duration={2000} onSetActive={this.handleSetActive}>
                                 <button className="btn btn-success" onClick={this.handleClickFormAdd}>Tambah Kendaraan Baru</button><br/><br/>
                              
                                
                              </Link>
                              </div>





                             
                              
                                 
                          </div><br/>
                        
                        <div className="row">
                          <div className="col-md-12">
                             <table id="example1"  className="table table-bordered table-striped dataTable">
                          <thead>
                            <tr role="row">
                              <th className="col-md-1">No</th>
                              <th className="col-md-2">Nopol</th>
                              <th className="col-md-3">Location</th>
                              <th className="col-md-1">Waktu</th>
                              <th className="col-md-1">Group</th>
                              <th className="col-md-1">Engine</th>
                              <th className="col-md-1">Speed</th>
                              <th className="col-md-1">Driver</th>
                              <th className="col-md-1">Action</th>
                            </tr>
                          </thead>
                          <tbody>


                              <tr>
                              <td>1</td>
                                <td>PJR Group</td>
                                 <td>Korlantas</td>
                                 <td>Korlantas</td>
                                 <td>Korlantas</td>
                                 <td>Korlantas</td>
                                 <td>Korlantas</td>
                                 <td>Korlantas</td>
                                  <td>
                                    <tr>
                                        <td>
                                         <Link activeClass="active" to="test1" spy={true} smooth={true} offset={50} duration={2000} onSetActive={this.handleSetActive}>
                                            <small className="label pull-right bg-green" onClick={()=>this.setState({showFormEdit:!this.state.showFormEdit,showFormAdd:false})}><i className="fa fa-edit biggerFont makePointer"></i>
                                            </small>
                                            </Link>
                                        </td>&nbsp;&nbsp;
                                        <td>
                                            <small className="label pull-right bg-red" onClick={()=>this.setState({showSwalDeleteAlert:true})}>
                                              <i className="fa fa-trash biggerFont makePointer"></i>
                                            </small>
                                        </td>
                                    </tr>
                                  </td>
                              </tr>

                             </tbody>
                          <tfoot>
                          </tfoot>
                        </table>
                          </div>
                        </div>

                       
                          </div>
                      </div>
                    </div>
                  </div>

 <Element name="test1" className="element">

                  {this.state.showFormAdd ? 
                   <div className="box customPadding"><h2>Form Add</h2>
                      <div className="row">

                      
                        <div className="col-md-6">
                            
                                    <div className="form-group">
                                      <label for="exampleInputPassword1">Nopol</label>
                                      <input type="text" className="form-control" id="exampleInputPassword1" placeholder="" />
                                    </div>

                                    <div className="form-group">
                                      <label for="exampleInputPassword1">Posisi</label>
                                      <input type="text" className="form-control" id="exampleInputPassword1" placeholder="" />
                                    </div>

                                    <div className="form-group">
                                      <label for="exampleInputPassword1">Simcard</label>
                                      <input type="text" className="form-control" id="exampleInputPassword1" placeholder="" />
                                    </div>

                                    <div className="form-group">
                                      <label for="exampleInputPassword1">Ikon</label><br/>
                                      <select title="Select your surfboard" className="selectpicker">
                                          <option>Select...</option>
                                          <option data-thumbnail="dist/img/credit/mestro.png">Chrome</option>
                                          <option data-thumbnail="dist/img/credit/mestro.png">Safari</option>
                                          <option data-thumbnail="dist/img/credit/mestro.png">Firefox</option>
                                          <option data-thumbnail="dist/img/credit/mestro.png">Opera</option>
                                        </select>
                                    </div>

                                     <div className="form-group">
                                      <label for="exampleInputEmail1">Group</label><br/>
                                          <select className="form-control select2" ref="typeRef" onChange={this.handlChange}>
                                           
                                            <option value="grapefruit">Grapefruit</option>
                                            <option value="lime">Lime</option>
                                            <option value="coconut">Coconut</option>
                                            <option value="mango">Mango</option>
                                          </select>
                                      </div>

                                    <div className="form-group">
                                      <label for="exampleInputPassword1">Driver</label>
                                      <input type="text" className="form-control" id="exampleInputPassword1" placeholder="" />
                                    </div>

                                    <div className="form-group">
                                      <label for="exampleInputPassword1">Odometer</label>
                                      <input type="text" className="form-control" id="exampleInputPassword1" placeholder="" />
                                    </div>

                                    <div className="form-group">
                                      <label for="exampleInputPassword1">Route</label>
                                      <input type="text" className="form-control" id="exampleInputPassword1" placeholder="" />
                                    </div>

                                    <div className="form-group">
                                      <label for="exampleInputPassword1">Note</label>
                                      <input type="text" className="form-control" id="exampleInputPassword1" placeholder="" />
                                    </div>

                                    <div className="form-group">
                                      <label for="exampleInputPassword1">Km Per Liter</label>
                                      <input type="text" className="form-control" id="exampleInputPassword1" placeholder="" />
                                    </div>

                                    <div className="form-group">
                                      <label for="exampleInputPassword1">Full Tank</label>
                                      <input type="text" className="form-control" id="exampleInputPassword1" placeholder="" />
                                    </div>


                                     <div className="box-footer">
                                    <button type="submit" className="btn btn-primary" onClick={()=>this.setState({showSwalUpdateSuccess:!this.state.showSwalUpdateSuccess})}>Add</button>&nbsp;&nbsp;
                                      <button className="btn btn-danger" onClick={()=>this.setState({showFormAdd:false})}>Cancel</button>
                                  </div>

                      
                      </div>

                         <div className="col-md-6">
                              <div className="form-group">
                                     <label for="exampleInputPassword1">Gambar Unit</label>
                                       <div className="myDivUpload">
                                        <input type="file" className="input-upload btn btn-info custom-file-input customChooseFile"  />
                                        <div className="previewImage" >
                                          <img src="dist/img/avatar5.png" className="imgP" height="150px" />
                                        </div>
                                       </div>
                                    </div>
                         </div>



                         


                    </div>
                  </div>

:null}


 {this.state.showFormEdit ? 
                   <div className="box customPadding"><h2>Form Edit</h2>
                      <div className="row">

                      
                        <div className="col-md-6">
                            
                                    <div className="form-group">
                                      <label for="exampleInputPassword1">Nopol</label>
                                      <input type="text" className="form-control" id="exampleInputPassword1" placeholder="" />
                                    </div>

                                    <div className="form-group">
                                      <label for="exampleInputPassword1">Posisi</label>
                                      <input type="text" className="form-control" id="exampleInputPassword1" placeholder="" />
                                    </div>

                                    <div className="form-group">
                                      <label for="exampleInputPassword1">Simcard</label>
                                      <input type="text" className="form-control" id="exampleInputPassword1" placeholder="" />
                                    </div>

                                    <div className="form-group">
                                      <label for="exampleInputPassword1">Ikon</label><br/>
                                      <select title="Select your surfboard" className="selectpicker">
                                          <option>Select...</option>
                                          <option data-thumbnail="dist/img/credit/mestro.png">Chrome</option>
                                          <option data-thumbnail="dist/img/credit/mestro.png">Safari</option>
                                          <option data-thumbnail="dist/img/credit/mestro.png">Firefox</option>
                                          <option data-thumbnail="dist/img/credit/mestro.png">Opera</option>
                                        </select>
                                    </div>

                                     <div className="form-group">
                                      <label for="exampleInputEmail1">Group</label><br/>
                                          <select className="form-control select2" ref="typeRef" onChange={this.handlChange}>
                                           
                                            <option value="grapefruit">Grapefruit</option>
                                            <option value="lime">Lime</option>
                                            <option value="coconut">Coconut</option>
                                            <option value="mango">Mango</option>
                                          </select>
                                      </div>

                                    <div className="form-group">
                                      <label for="exampleInputPassword1">Driver</label>
                                      <input type="text" className="form-control" id="exampleInputPassword1" placeholder="" />
                                    </div>

                                    <div className="form-group">
                                      <label for="exampleInputPassword1">Odometer</label>
                                      <input type="text" className="form-control" id="exampleInputPassword1" placeholder="" />
                                    </div>

                                    <div className="form-group">
                                      <label for="exampleInputPassword1">Route</label>
                                      <input type="text" className="form-control" id="exampleInputPassword1" placeholder="" />
                                    </div>

                                    <div className="form-group">
                                      <label for="exampleInputPassword1">Note</label>
                                      <input type="text" className="form-control" id="exampleInputPassword1" placeholder="" />
                                    </div>

                                    <div className="form-group">
                                      <label for="exampleInputPassword1">Km Per Liter</label>
                                      <input type="text" className="form-control" id="exampleInputPassword1" placeholder="" />
                                    </div>

                                    <div className="form-group">
                                      <label for="exampleInputPassword1">Full Tank</label>
                                      <input type="text" className="form-control" id="exampleInputPassword1" placeholder="" />
                                    </div>


                                     <div className="box-footer">
                                    <button type="submit" className="btn btn-primary" onClick={()=>this.setState({showSwalUpdateSuccess:!this.state.showSwalUpdateSuccess})}>Update</button>&nbsp;&nbsp;
                                      <button className="btn btn-danger" onClick={()=>this.setState({showFormEdit:false})}>Cancel</button>
                                  </div>

                      
                      </div>

                         <div className="col-md-6">
                              <div className="form-group">
                                     <label for="exampleInputPassword1">Gambar Unit</label>
                                       <div className="myDivUpload">
                                        <input type="file" className="input-upload btn btn-info custom-file-input customChooseFile"  />
                                        <div className="previewImage" >
                                          <img src="dist/img/avatar5.png" className="imgP" height="150px" />
                                        </div>
                                       </div>
                                    </div>
                         </div>



                         


                    </div>
                  </div>

:null}



</Element>


                </div>
            </div>
         </div>
    );
  }
}

export default VehicleSetting;
/*


*/


