import React, { Component } from 'react';

import {Switch,Route} from 'react-router-dom';
import About from '../pages/About';
import Contact from '../pages/Contact';
import Dashboard from '../pages/Dashboard';
import VehicleGroup from '../pages/VehicleGroup';
import Login from '../pages/Login';
import Report from '../pages/Report';
import Profile from '../pages/Profile';
import UserManagement from '../pages/UserManagement';
import VehicleIcon from '../pages/VehicleIcon';
import VehicleSetting from '../pages/VehicleSetting';



export default ()=>(

<Switch>
	<Route exact path="/" component={Login}/>
	<Route exact path="/dashboard" component={Dashboard}/>
	<Route path="/contact" component={Contact}/>
	<Route path="/vgroup" component={VehicleGroup}/>

	<Route path="/report" component={Report}/>
	<Route path="/profile" component={Profile}/>
	<Route path="/user_management" component={UserManagement}/>
	<Route path="/vicon" component={VehicleIcon}/>
	<Route path="/vsetting" component={VehicleSetting}/>
</Switch>

)



