import React, { Component } from 'react';
import {Consumer} from '../context/App-Context';
import {withRouter} from 'react-router-dom'

class Login extends Component {
    
    constructor(){
      super()
      this.state = {
        login : true
      }
    }

    // componentWillUnmount() {
    //   window.location.reload()
    // }

   //  redirectReload = () => {
   //    this.props.history.push("");
   // }

    handleClickLogin = ()=>{
      this.props.history.push("/dashboard")
    }

  render(){
    console.log(this.props)
    return (

          <div className="box box-info customLoginBox">
           
                <div className="login-box-body">

                
                   <img src="dist/img/logo-login.png" className="" alt="logo-login"/>
                   
                    <form className="form-horizontal">
                      <div className="box-body">
                        <div className="form-group redbg">
                         

                          <div className="col-sm-12">
                            <input type="email" className="form-control" id="inputEmail3" placeholder="Username"/>
                          </div>
                        </div>
                        <div className="form-group">
                         

                          <div className="col-sm-12">
                            <input type="password" className="form-control" id="inputPassword3" placeholder="Password"/>
                          </div>
                        </div>
                        <div className="form-group">
                          <div className="col-sm-offset-2 col-sm-10">
                           
                          </div>
                        </div>
                      </div>
                    
                      <div className="box-footer">
                       
                        <button type="submit" className="btn btn-info pull-left" onClick={()=>this.handleClickLogin()} >Login</button>

                      </div>
                     
                    </form>
                 

        </div>
      </div>


    );
  }
}

export default withRouter(Login);
