import React, { Component } from 'react';



class Profile extends Component {
  constructor(){
    super()
    this.state = {
      showHoverImage:false
    }
  }
  render() {
    return (
        <div>
            <section className="content-header">
                   <ol className="breadcrumb">
                   
                    <li className="">Profile</li>
                   </ol>
            </section>
            <div className="content"> 
                <div className="row customPadding">
                   <div className="box customPadding">
                      <div className="row">
                        <div className="col-md-7">
                           
                            <form role="form">
                                  <div className="box-body">

                                    <div className="form-group">
                                     <label for="exampleInputPassword1">Logo</label>
                                       <div className="myDivUpload">
                                        <input type="file"  onMouseOver={()=>this.setState({showHoverImage:true})} onMouseOut={()=>this.setState({showHoverImage:false})} className="input-upload btn btn-info custom-file-input customChooseFile" />
                                        
                                        <div className="previewImage">

                                          <img src="dist/img/avatar5.png"  height="150px"/>
                                        </div>
                                        
                                       </div>
                                    </div>

                                   <div className="form-group">
                                     <label for="exampleInputPassword1">Logo Utama</label>
                                       <div className="myDivUpload">
                                        <input type="file"  onMouseOver={()=>this.setState({showHoverImage:true})} onMouseOut={()=>this.setState({showHoverImage:false})} className="input-upload btn btn-info custom-file-input customChooseFile"  />
                                        <div className="previewImage" >
                                          <img src="dist/img/avatar5.png" className="imgP" height="150px" />
                                        </div>
                                       </div>
                                    </div>

                                    <div className="form-group">
                                      <label for="exampleInputPassword1">Isi Profile</label>
                                      <input type="text" className="form-control" id="exampleInputPassword1" placeholder="" />
                                    </div>

                                    <div className="form-group">
                                      <label for="exampleInputPassword1">Kontak</label>
                                      <input type="text" className="form-control" id="exampleInputPassword1" placeholder="" />
                                    </div>

                                    <div className="form-group">
                                      <label for="exampleInputPassword1">Password</label>
                                      <input type="password" className="form-control" id="exampleInputPassword1" placeholder="" />
                                    </div>
                                   
                                   <div className="form-group">
                                      <label for="exampleInputPassword1">New Password</label>
                                      <input type="password" className="form-control" id="exampleInputPassword1" placeholder="" />
                                    </div>

                                     <div className="form-group">
                                      <label for="exampleInputPassword1">Confirm Password</label>
                                      <input type="password" className="form-control" id="exampleInputPassword1" placeholder="" />
                                    </div>
                                   
                                  </div>
                                 

                                  <div className="box-footer">
                                    <button type="submit" className="btn btn-primary">Update</button>
                                  </div>
                                </form>

                      
                      </div>
                    </div>
                  </div>
                </div>
            </div>
         </div>
    );
  }
}

export default Profile;




