import React,{ Component } from 'react';
import {Redirect} from 'react-router-dom';



export const AppContext = React.createContext();
export const {Consumer,Provider} = AppContext;


export class AppProvider extends Component{
	constructor(props){
		super(props)
		this.state = {
			title : 'Deden Damdani',
			loginContext:true
		}
	}

	setTitle = ()=>{
		this.setState({title : 'Indonesia'})
	}
	handleLogin = ()=>{
		this.setState({loginContext:true})
		
	}
	handleLogout = ()=>{
		this.setState({loginContext:false})
	}
	render(){
		return(
			<Provider value={{
				myTitle : this.state.title,
				setTitle : this.setTitle,
				myBreadcrumb:this.state.breadcrumb,
				loginContext : this.state.loginContext,
				handleLogin : this.handleLogin,
				handleLogout : this.handleLogout
			}}>
			{this.props.children}
			</Provider>
		)
	}
}


