import React, { Component } from 'react';
const google = window.google;

class Contact extends Component {
	constructor(){
		super()
		this.state = {
			bread : 'Contact'
		}
	}

	componentDidMount(){
			this.map = new google.maps.Map(this.refs.map,{
				center :{lat: -6.5656, lng: 106.5656},
				zoom:10,
				mapTypeId: google.maps.MapTypeId.ROADMAP
			})
			let infowindow = new google.maps.InfoWindow({
		          content: '<p>halo euy</p>'
		        });
			var myLatlng = new google.maps.LatLng({lat: -6.5656, lng: 106.5656});
			this.mrkr = new google.maps.Marker({
		            position: myLatlng,
		            map: this.map,
		            animation: google.maps.Animation.DROP,
		            title: 'Uluru (Ayers Rock)'
		          	})
			
			this.mrkr.addListener('click', function() {
		          infowindow.open(this.map,this)
		        });
	}

	

  render() {
    return (
    	 <div>
            <section className="content-header">
                   <ol className="breadcrumb">
                    <li><a href="#"><i className="fa fa-dashboard"></i>Home</a></li>
                    <li className="">Vehicle Group</li>
                   </ol>
            </section>
            <div className="content"> 
                <div className="row whiteBg customPadding">
                	 <div className="box customPadding">
                     	<div className="row">
                     		<div className="col-md-12">
               				 <div id="map" ref="map" style={{height:500}}></div>
                			</div>
                		</div>
                	</div>
                </div>
            </div>
         </div>
    	
    );
  }
}

export default Contact;



