import React, { Component } from 'react';
import {Consumer} from '../context/App-Context';
import DatePicker from 'react-datepicker';
import moment from 'moment';
import 'react-datepicker/dist/react-datepicker.css';


class Report extends Component {
  constructor(){
    super()
    this.state = {
      date : new Date(),
      startDate: moment(),
      type:'',
      nopol:'',
      dateRange:''
    },
    this.myData
  }


componentDidMount() {
    var script = document.createElement('script');
    script.src = "dist/js/pages/custom.js";
    document.getElementsByTagName('head')[0].appendChild(script);
}
 

  

  handleSubmit = ()=>{
    this.setState({type:this.refs.typeRef.value},()=>alert(this.state.type))
    
  }

  render() {
 
    return (
     <div>
     
            <section className="content-header">
                   <ol className="breadcrumb">
                    
                    <li className="">Laporan</li>
                   </ol>
            </section>
            <div className="content"> 
                <div className="row customPadding">
                   <div className="box customPadding">


                      <div className="row">

                     
                        <div className="col-md-7">
                       
                          <div className="box-body">
                              
                              <div className="form-group">
                                <label for="exampleInputEmail1">Tipe Laporan</label>
                                    <select className="form-control select2" ref="typeRef" onChange={this.handlChange}>
                                     
                                      <option value="grapefruit">Grapefruit</option>
                                      <option value="lime">Lime</option>
                                      <option value="coconut">Coconut</option>
                                      <option value="mango">Mango</option>
                                    </select>
                                </div>
                              
                              <div className="form-group">
                                  <label for="exampleInputPassword1">Nopol</label>
                                  <select className="form-control select2" ref="nopolRef">
                                      <option selected="selected">Select Unit</option>
                                      <option>Alaska</option>
                                      <option>California</option>
                                      <option>Delaware</option>
                                      <option>Tennessee</option>
                                      <option>Texas</option>
                                      <option>Washington</option>
                                    </select>
                                </div>


                                <div className="form-group">
                                  <label>Tanggal Mulai dan Akhir :</label>
                                  <div className="input-group">
                                    <div className="input-group-addon">
                                      <i className="fa fa-calendar-o"></i>
                                    </div>
                                    <input type="text" className="form-control pull-right" id="reservation" ref="rangeDate" onChange={this.handlChange}/>
                                  </div>
                                 </div>


                                

                                 <div className="form-group">
                                  <label for="exampleInputPassword1">Pengurutan</label>
                                 
                                    <select className="form-control select2">
                                      <option selected="selected">Normal Type</option>
                                      <option>Alaska</option>
                                    </select>
                                
                                </div>
                                
                                </div>
                                <div className="box-footer">
                                <button type="submit" className="btn btn-primary" onClick={this.handleSubmit}>Submit</button>
                              </div>
                           
                        </div>
                      </div>


                  </div>
                </div>
            </div>
         </div>
    );
  }
}

export default Report;



