import React, { Component } from 'react';
import Modal from 'react-responsive-modal';
const google = window.google;


export default class MapDashboar extends Component {
	constructor(props){
		super(props)
		this.state = {
			ll : [
			        {lat: 52.511, lng: 13.447},
			        {lat: 52.549, lng: 13.422},
			        {lat: 52.497, lng: 13.396},
			        {lat: 52.517, lng: 13.394}
			      ],
			 lat : -6.5656,
			 lon : 106.5656,
			 allMarker:'',
			 forDelete:{lat: 52.549, lng: 13.422},
			 showModal:false,
			 isinya:[]   
			
		},
		this.mar = [],
		this.contentForModal = []


	}

	componentDidMount(){
		let that = this
		this.map = new google.maps.Map(this.refs.map,{
			center :{lat: this.state.lat, lng: this.state.lon},
			zoom:10
			
			})
		for (var i = 0; i < this.props.allmarks.length; i++){
			 var myLatlng = new google.maps.LatLng(this.props.allmarks[i].coor);
			 this.mrkr = new google.maps.Marker({
		        position: myLatlng,
		        map: this.map,
		        animation: google.maps.Animation.DROP
		        })
			this.mar.push(this.mrkr)
		    this.contentForModal.push(this.props.allmarks[i])
		        // let infowindow = new google.maps.InfoWindow({
		        //   content:this.props.allmarks[i].info
		        // });
		    this.mrkr.addListener('click', function() {
		         	//infowindow.open(this.map,this)
		         	//that.setState({showModal:true})
		    for (var i = 0; i < that.contentForModal.length; i++) {
		         	let haha =  new google.maps.LatLng(that.contentForModal[i].coor);
		         		if(haha.lat() == this.position.lat() && haha.lng() == this.position.lng()){
		         				that.setState({isinya:that.contentForModal[i].info,showModal:true})
		         			}
		  		}
		    });
	    }
	}

	onCloseModal=()=>{
		this.setState({showModal:false})
	}

	shouldComponentUpdate(nextProps,nextState){
		let that = this
		if(this.props.allmarks !== nextProps.allmarks){
				for (var i = 0; i < this.mar.length; i++){
	      	 			this.mar[i].setMap(null)
	      	 		}
		        for (var i = 0; i < nextProps.allmarks.length; i++){
						var myLatlng = new google.maps.LatLng(nextProps.allmarks[i].coor);
						this.mrr = 	new google.maps.Marker({
				            position: myLatlng,
				            map: this.map,
				            animation: google.maps.Animation.DROP
				          	})
				        this.mar.push(this.mrr)
				        this.contentForModal.push(nextProps.allmarks[i])
				        // let infowindow = new google.maps.InfoWindow({
				        //   content:nextProps.allmarks[i].info
				        // });
				        this.mrr.addListener('click', function() {
				         	for (var i = 0; i < that.contentForModal.length; i++) {
		         			let haha =  new google.maps.LatLng(that.contentForModal[i].coor);
			         			if(haha.lat() == this.position.lat() && haha.lng() == this.position.lng()){
			         				that.setState({isinya:that.contentForModal[i].info,showModal:true})
			         				}
		  					}
				        });
			        }
	        this.map.panTo(myLatlng)
		}
		
	    return true
	}

	render(){
		return(
			
			<div id="map" ref="map" style={{height:500}}>
				<Modal open={this.state.showModal} onClose={this.onCloseModal} center>
		         <br/>
		         <h3>Detail Vehicle</h3>
		          <table id="example1"  className="table table-bordered table-striped dataTable">
                    <tbody>
						 
						 <tr>
                           <td className="col-md-1">Nopol</td>
                            <td className="col-md-2">B-9898-TMF</td>
                         </tr>
                         <tr>
                           <td className="col-md-1">Date</td>
                            <td className="col-md-2">21 Mei 2018</td>
                         </tr>
                         <tr>
                           <td className="col-md-1">Time</td>
                            <td className="col-md-2">Siank</td>
                         </tr>
                         <tr>
                           <td className="col-md-1">Location</td>
                            <td className="col-md-2">Jakarta</td>
                         </tr>
                          <tr>
                           <td className="col-md-1">Driver</td>
                            <td className="col-md-2">Test</td>
                         </tr>
                          <tr>
                           <td className="col-md-1">Longitude</td>
                            <td className="col-md-2">9798789789789</td>
                         </tr>
                          <tr>
                           <td className="col-md-1">Latitude</td>
                            <td className="col-md-2">34534534534</td>
                         </tr>
                          <tr>
                           <td className="col-md-1">Engine</td>
                            <td className="col-md-2">Standard: 8.0L W16 quad-turbocharger 1001 PS Super Sport: 1200 PS</td>
                         </tr>
                          <tr>
                           <td className="col-md-1">Speed</td>
                            <td className="col-md-2">300 kmph</td>
                         </tr>
                          <tr>
                           <td className="col-md-1">Even</td>
                            <td className="col-md-2"></td>
                         </tr>
                         
                         
                    </tbody>
                  </table>
                  <button className="btn btn-default" onClick={()=>this.setState({showModal:false})}>Close</button>
		        </Modal>
			</div>
			
		)
	}
   
}
