import React,{Component}from 'react';
import { withRouter } from 'react-router-dom';
import MapDashboard from '../components/MapDashboard';

class Dasboard extends Component{

  constructor(){
    super()
    this.state = {
      showLi : false,
      
       ll : [
              {
                city:'bandung',
                info:'<p>halo bandung</p>',
                coor:{lat: -6.5656, lng: 106.5656}
                },
                {
                 city:'jakrta',
                 info:'<p>halo jakrta</p>',
                 coor:{lat: -6.78787, lng: 106.7676}
                },
                {
                city:'lampung',
                info:'<p>halo lampung</p>',
                coor:{lat: -6.5656, lng: 106.79797}
                }
            ],
         ll2: [
               {
                city:'bandung',
                info:'<p>halo bandung</p>',
                coor:{lat: -6.5656, lng: 106.5656}
                },
                {
                 city:'jakrta',
                 info:'<p>halo jakrta</p>',
                 coor:{lat: -6.78787, lng: 106.7676}
                },
                {
                city:'lampung',
                info:'<p>halo lampung</p>',
                coor:{lat: -6.5656, lng: 106.79797}
                }
            ]          
    },
    this.mar = []
  }

  // UNSAFE_componentWillMount() {
  //   if(this.props.location.state){
  //      if(this.props.location.state.hey == 'sil'){
  //         //alert('before')
  //         window.location.reload()
  //       }
  //    } setTimeout(()=>(this.props.history.replace({ ...this.props.history })),100)
  // }


  handleOneMarker =(val)=>{
    let tmp = []
    tmp.push(val)
    this.setState({ll:tmp})
  }


  handleAllMarker = ()=>{
    this.setState({ll:[
              {
                city:'bandung',
                info:'<p>halo bandung</p>',
                coor:{lat: -6.5656, lng: 106.5656}
                },
                {
                 city:'jakrta',
                 info:'<p>halo jakrta</p>',
                 coor:{lat: -6.78787, lng: 106.7676}
                },
                {
                city:'lampung',
                info:'<p>halo lampung</p>',
                coor:{lat: -6.5656, lng: 106.79797}
                }
            ]})
  }

  renderAllMarker = ()=>{
    return (
        this.state.ll2.map((val)=>{
          return(<li className="" key="val.coor">
                 <div>
                    <a href="javascript:void(0)"  onClick={()=>this.handleOneMarker(val)}>{val.city}</a>
                  </div>
                </li>
            )
        })
      )
  }

	render(){
     
		return(<div>
               <section className="content-header">
                   <ol className="breadcrumb">
                   
                    <li className="">Dasboard</li>
                   </ol>
                </section>
               <div className="content">
                 
  			           <div className="row customPadding">
                       <div className="box customPadding">
                          <div className="row">
                             <div className="col-md-2">
                                <div className="box-header with-border">
                                   <div className="input-group">
                                       <input type="text" name="q" className="form-control" placeholder="Search..."/>
                                       <span className="input-group-btn">
                                       <button type="submit" name="search" id="search-btn" className="btn btn-flat"><i className="fa fa-search whiteColorText"></i></button>
                                       </span>
                                    </div>
                                  </div>
                                    <div className="box-body  boxOverflow" >
                                       
                                      <ul className="products-list product-list-in-box" data-widget="tree">
                                          <li className="treeview">
                                            <a href="javascript:void(0)" onClick={()=>this.setState({showLi:!this.state.showLi})}><i className="fa fa-folder biggerFont"></i><span className="biggerFont"> Group 1</span></a>
                                            {this.state.showLi ? <ul>{this.renderAllMarker()}</ul> : null}
                                          </li>
                                       </ul>


                                
                                    </div>
                                       <div className="box-footer text-center" >
                                          <a href="javascript:void(0)" className="uppercase" onClick={this.handleAllMarker}>Semua Kendaraan</a>
                                       </div>
                                    </div>
                                <div className="col-md-10">
                                 <MapDashboard allmarks={this.state.ll} showAllMarker={this.handleAllMarker}/>
                               </div>
                           </div>
                      </div>
                 </div>
              </div>
          </div>
		)
	}
}
export default withRouter(Dasboard);

