import React, { Component } from 'react';
import {Consumer} from '../context/App-Context';



class About extends Component {
  render() {
    return (
      <div>
      <Consumer>
        {(con)=>{
          return(
            <div>
              <h2>hei {con.myTitle}</h2>
              <button onClick={()=>con.setTitle()}>test klik</button>
            </div>  
          ) 
        }}
        </Consumer>
     
      </div>
    );
  }
}

export default About;
