import React, { Component } from 'react';
import {Link,withRouter} from 'react-router-dom';
import {Consumer} from '../context/App-Context';
class Sidebar extends Component {


   handleRedirect = ()=>{
      this.props.history.push("/report")
    }
  render() {
    return (
      <aside className="main-sidebar">
      <div className="sidebar">
      
      <div className="user-panel">
      <div className="pull-left image">
      <img src="dist/img/avatar5.png" className="img-circle" alt="User Image"/>
      </div>
      <div className="pull-left info">
      <p><Consumer>{(con)=>con.myTitle}</Consumer></p>
      
      </div>
      </div>
      
      
      <ul className="sidebar-menu" data-widget="tree">
          
      

      <li>
      <a href="javascript:void(0)">
      <i className="fa fa-dashboard"></i> <span><Link to='/dashboard'><i className=""></i>Dasboard</Link></span>
      <span className="pull-right-container">
      </span>
      </a>
      </li>

      <li>
      <a href="javascript:void(0)">
      <i className="fa fa-book"></i><span><Link to='/report'><i className=""></i>Laporan</Link></span>
      <span className="pull-right-container">
      </span>
      </a>
      </li>


       <li>
      <a href="javascript:void(0)">
      <i className="fa fa-object-group"></i> <span><Link to='/vgroup'><i className=""></i>Grup Kendaraan</Link></span>
      <span className="pull-right-container">
      </span>
      </a>
      </li>


       <li>
      <a href="javascript:void(0)">
      <i className="fa fa-gear "></i> <span><Link to='/vsetting'><i className=""></i>Pengaturan Kendaraan</Link></span>
      <span className="pull-right-container">
      </span>
      </a>
      </li>


       <li>
      <a href="javascript:void(0)">
      <i className="fa fa-car"></i> <span><Link to='/vicon'><i className=""></i>Icon Kendaraan</Link></span>
      <span className="pull-right-container">
      </span>
      </a>
      </li>


       <li>
      <a href="javascript:void(0)">
      <i className="fa fa-user"></i> <span><Link to='/user_management'><i className=""></i>Pengelolaan Pengguna</Link></span>
      <span className="pull-right-container">
      </span>
      </a>
      </li>
     
      
      </ul>
      </div>
      </aside>
      );
    }
}

export default withRouter(Sidebar);



